\nbody {\r\n    padding: 0;\r\n    margin: 0;\r\n    height: 100vh;\r\n    position: relative;\r\n}\r\n\r\n#page-contents{\r\n    min-height: 100vh;\r\n    height: fit-content;\r\n}\r\n\r\n@media (min-width: 1080px){\r\n    html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed,  figure, figcaption, footer, header, hgroup,  menu, nav, output, ruby, section, summary, time, mark, audio, video {\r\n        cursor: none;\r\n    }  \r\n}","//cursor\r\n.cursor{\r\n    z-index: 10;\r\n    pointer-events: none;\r\n\r\n    display: none;\r\n\r\n    width: 30px;\r\n    height: 30px;\r\n    position: fixed;\r\n    border-radius: 50%;\r\n\r\n    backdrop-filter: invert(100%) grayscale(100%);\r\n    -webkit-backdrop-filter: invert(100%) grayscale(100%);\r\n    filter: brightness(150%) contrast(150%);\r\n\r\n    overflow: hidden;\r\n}\r\n\r\n//pre-loader\r\n\r\n#preload-container{\r\n    z-index: 10;\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: $bgColourDark;\r\n    background-image: url('/src/assets/bg_grid.svg');\r\n    background-size: 135rem;\r\n    background-repeat: repeat;\r\n    background-position: center top;\r\n}\r\n\r\n#preload-info{\r\n    width: 80%;\r\n    height: 15rem;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n}\r\n\r\n#loader-icon{\r\n    transform-style: preserve-3d;\r\n    animation: spin 6s infinite linear;\r\n    height: 5rem;\r\n    width: 5rem;\r\n    margin-bottom: 2rem;\r\n    position: relative;\r\n    align-self: center;\r\n    \r\n    div{\r\n        height: 5rem;\r\n        width: 5rem;\r\n        position: absolute;\r\n        display: block;\r\n        border: $borderStrokeWidth solid $lineColourLight;\r\n        box-shadow: 0 0 15px 0px $lineColourLight;\r\n    }\r\n\r\n    .top{\r\n        transform: rotateX(90deg); \r\n        margin-top: -40px; \r\n    }\r\n\r\n    .right {\r\n        transform: rotateY(90deg); \r\n        margin-left: 40px;\r\n    }\r\n     \r\n    .bottom {\r\n        transform: rotateX(-90deg); \r\n        margin-top: 40px;\r\n    }\r\n     \r\n    .left {\r\n        transform: rotateY(-90deg); \r\n        margin-left: -40px;\r\n    }\r\n     \r\n    .front {\r\n        transform: translateZ(40px);\r\n    }\r\n     \r\n    .back {\r\n        transform: translateZ(-40px) rotateX(180deg);\r\n    }\r\n\r\n    @keyframes spin {\r\n        0% {-webkit-transform: rotateX(-50deg) rotateY(20deg);}\r\n        100% {-webkit-transform: rotateX(-50deg) rotateY(740deg);}\r\n    }\r\n}\r\n\r\n#loading-text{\r\n    font-size: $bodyTextSize * 1.5;\r\n    font-family: $inputSans;\r\n    text-align: center;\r\n    position: relative;\r\n}\r\n\r\n#loading-info{\r\n    width: 100%;\r\n    text-align: center;\r\n    position: relative;\r\n}\r\n\r\n//bg colours\r\n\r\n.blue{\r\n    background-color: $bgColourBlue;\r\n}\r\n\r\n.grey{\r\n    background-color: $lineColourLight;\r\n}\r\n\r\n.black{\r\n    background-color: $bgColourDark;\r\n}\r\n\r\n.gradient{\r\n    background-color: linear-gradient(0deg, #0092bd 0%, #171717 44%, #171717 68%, #171717 100%); \r\n}\r\n\r\n//bg grids \r\n\r\n.grid-background{\r\n    background-image: url('/src/assets/bg_grid.svg');\r\n    background-size: 135rem;\r\n    background-repeat: repeat;\r\n    background-position: center top;\r\n}\r\n\r\n.grid-light{\r\n    background-image: url('/src/assets/bg_grid_light.svg');\r\n}\r\n\r\n//text elements:\r\n\r\n#primary-header{\r\n    //limits width of header for homepage\r\n    max-width: 15rem;\r\n    user-select: none;\r\n}\r\n\r\n.header{\r\n    color: $lineColourLight;\r\n    font-family: $inputSans;\r\n    font-